@import "../../../assets/styles/index.scss";

.item {
  box-shadow: 0 0.25rem 0.5rem 0 $color-shadow;
  display: flex;
  border-radius: 0.25rem;
  max-width: 23.75rem;
  .img {
    width: 7.75rem;
    height: 7.75rem;
    .image {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  .content {
    padding: 1rem 1.5rem;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    @include media-tablet {
      padding: 0.8rem 1.3rem;
    }
    @include media-mobile {
      padding: 0.5rem 1rem;
    }

    .status {
      color: $color-addition-bright;
      background: $color-addition;
      padding: 0.5rem 0.75rem;
      font-size: 0.75rem;
      line-height: 1rem;
      text-align: center;
      align-self: flex-start;
      letter-spacing: 0.1rem;
      font-variant: small-caps;
      @include media-tablet {
        padding: 0.3rem 0.5rem;
        @include font($size: 0.7rem, $height: 0.8rem);
      }
    }
    .name {
      color: $color-black;
      font-size: 1rem;
      line-height: 1.5rem;
      margin-bottom: 0;
      @include media-tablet {
        @include font($height: 1.2rem, $size: 0.9rem);
      }
    }
    .date {
      font-size: 0.75rem;
      line-height: 1.5rem;
      opacity: 0.4;
      color: $color-black;
      @include media-tablet {
        @include font($height: 1.2rem, $size: 0.6rem);
      }
    }
  }
}
.row {
  box-shadow: 0 0.25rem 0.5rem 0 $color-shadow;
  display: flex;
  border-radius: 0.25rem;
  align-items: center;
  height: 5rem;

  .imgRow {
    width: 4rem;
    height: 4rem;
    margin-right: 4rem;
    .imageRow {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  .contentRow {
    display: grid;
    width: 60%;
    grid-template-columns: repeat(3, 1fr);
    @include media-big-tablet {
      width: 100%;
    }
  }
  .statusRow {
    color: $color-addition-bright;
    background: $color-addition;
    padding: 0.5rem 0.75rem;
    font-size: 0.75rem;
    line-height: 1rem;
    text-align: center;
    align-self: flex-start;
    letter-spacing: 0.1rem;
    font-variant: small-caps;
    @include media-tablet {
      padding: 0.3rem 0.5rem;
      @include font($size: 0.7rem, $height: 0.8rem);
    }
    @media screen and(max-width:460px) {
      @include font($height: 0.6rem, $size: 0.7rem);
    }
  }
  .nameRow {
    @media screen and (max-width: 620px) {
      @include font($height: 1rem, $size: 0.9rem);
    }
    @media screen and(max-width:460px) {
      @include font($height: 0.8rem, $size: 0.7rem);
    }
  }
  .dateRow {
    @media screen and (max-width: 620px) {
      @include font($height: 1rem, $size: 0.9rem);
    }
    @media screen and(max-width:460px) {
      @include font($height: 0.8rem, $size: 0.7rem);
    }
  }
}
